<template>
<div style="margin-top:30px;">
  <div style="width:100% text-align: center;">
      <h1>添加课程</h1>
  </div>
 <div data-spy="scroll" data-target="#navbar-example" data-offset="0" 
   style="overflow-x:visible; position: relative;">
  <div style="width: 950px; float:left;">
  

   <table class="table table-bordered">
    <caption style="text-align: center;"><h4>进行中的课程</h4></caption>
    <thead>
      <tr>
        <th>课程名</th>
        <th>开课老师</th>
        <th>开课班级</th>
        <th>操作</th>
      </tr>
    </thead>
    <tbody>
    <tr  v-for="(course, index) in courses">
      <td>{{ course.name }}</td>
      <td>{{course.teacher_name}}</td>
      <td>{{course.grade}}</td>
      <td>
        <router-link :to="'/pages/classDetail/'+course.id" class="btn btn-default">查看详情</router-link>&nbsp&nbsp&nbsp
        <router-link :to="'/pages/classAsk/'+course.id" class="btn btn-default">添加课程</router-link>
      </td>
    </tr>
    </tbody>
    </table>
  </div>
  </div>

  </div>
</template>

<script type="text/javascript">
import axios from 'axios'
export default {
  name: 'add_course',
  data () {
    return {
      courses: [],
      fixid: 0,
      creid: 1,
      newClass: [],
      message: '',
      selected_index: -1
    }
  },
  methods: {
    jumpToAsk: function () {
      this.$router.push({path: '/pages/classAsk/' + this.courses[this.selected_index].id})
    },
    attemptJoinCourse: function (index) {
      this.selected_index = index
      console.log(this.selected_index)
      // this.code = this.courses[index].name
      // this.courses.splice(index, 1)
    },
    getAllCourse: function () {
      let self = this
      console.log('Axios')
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_all_joinable_course',
        method: 'post',
        data: {
          type: 'S2005',
          token: '1f5be77b086bc671b321a66ae4675330'
        },
        transformRequest: [function (data) {
        // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log(res)
        console.log(res.data.re)
        console.log(res.data.msg[0].name + '-----' + res.data.msg.length)
        self.courses = res.data.msg
        console.log(self.courses)
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  created () {
    this.getAllCourse()
  }
}
</script>

<style type="text/css">
th {
  text-align: center;
}
.ax_default {
  font-family:'微软雅黑 Light', '微软雅黑 Regular', '微软雅黑';
  font-weight:290;
  font-style:normal;
  font-size:14px;
  color:#333333;
  text-align:center;
  line-height:normal;
}
.droplist {
  color:#000000;
  text-align:left;
}
.ax_default_visible, .ax_default_unplaced{
    display: none;
    visibility: visible;
}


</style>


