<template>
<div>
  <router-link to="/add_course" class="btn btn-default" style="margin-right: 900px; margin-bottom: 10px;">返回</router-link>
  <div>
  <ul class="nav nav-tabs">
  <li class="active"><router-link :to="'/pages/classDetail/'+course_id">{{items.name}}问卷</router-link></li>
  </ul>
    <legend style="color:red;font-size:14px;text-align: center;">注意：请认真填写该预测问卷，该预测问卷将会影响你的成长起点。</legend>
  </div>

<div id="answer">
      <form @submit.prevent="submit">

        <h4 class="text-left">1、请问你的先修课程分数是多少？  </h4>
        <input style="width: 500px;" v-model="pre_course_score" class="form-control">

        <h4 class="text-left">2、学习完这门课程后，你对自己要求的的目标绩点在哪个分数段？</h4>
        <input style="width: 500px;" v-model="aim_score" class="form-control">

        <h4 class="text-left">3、学习这门课程后，你想收获些什么？想要达到什么学习目标？</h4>
         <textarea name="goal" v-model="aim_text" style="width:950px;height:250px" placeholder="不少于200字"></textarea>
         <br>
         <br>
         
        <div style="text-align: center;">
        <button type="button" class="btn btn-default" value="submit" data-toggle="modal" data-target="#myModal1" @click="joinCourse()">提交</button>
        </div>
      </form>
      </div>

      <div class="modal fade" id="myModal1" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-hidden="true">×</button>
            <h4 class="modal-title" id="myModalLabel">{{res_message}}</h4>
          </div>
          <div class="modal-footer">
            <router-link to="/add_course" button type="button" class="btn btn-primary"  data-dismiss="modal">确定</router-link>
          </div>
        </div>
        <!-- /.modal-content -->
      </div>
      <!-- /.modal-dialog -->
    </div>
    <!-- /.modal -->
</div>
</template>


<script type="text/javascript">

import axios from 'axios'
export default {
  data () {
    return {
      course_id: this.$route.params.id,
      items: [],
      classes: [],
      message: '',
      code: '',
      code_error: '',
      pre_course_score: '',
      aim_score: '',
      aim_text: '',
      res_message: ''
    }
  },
  methods: {
    joinCourse: function () {
      let self = this
      console.log(self.items)
      console.log('try######______------>', self.items.name)
      axios({
        url: 'https://diningx.cn/pa/public/api/student/join_course',
        method: 'post',
        data: {
          type: 'S2001',
          token: '1f5be77b086bc671b321a66ae4675330',
          course_id: self.course_id,
          class_name: self.class_name,
          pre_course_score: self.pre_course_score,
          aim_score: self.aim_score,
          aim_text: self.aim_text
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log(res)
        self.res_message = '添加课程成功！'
      }).catch(function (err) {
        self.res_message = '添加课程失败，请返回重试。'
        console.log(err)
      })
    },
    getData: function () {
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_course_info_by_id',
        method: 'post',
        data: {
          type: 'S2003',
          token: '1f5be77b086bc671b321a66ae4675330',
          course_id: self.course_id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log(self.course_id)
        console.log(res.data.msg)
        self.items = res.data.msg
        self.classes = self.items.classes
        console.log(self.classes)
        // console.log('------------->', self.items)
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  created () {
    this.getData()
  }
}
</script>
<style type="text/css">
a {
  text-decoration: none;
}



</style>


